<template>
	<div>
		<tips :message="message" :tips="showTips"></tips>
		<section id="nickName" v-show="nickNameShow">
			<div class="header">
				<router-link to="/" slot="left">
					<img src="static/img/left.svg" alt="goBack">
				</router-link>
			</div>
			
			<div class="infoBody">
				<h1></h1>
				<div class="avatarGroup">
					<input type="file" class="upload" name="file" @change="uploadImg($event)">
					<img :src="info.headerImg" alt="headPortrait" />
				</div>
				<div class="nickname">
					<h3></h3>
					<input v-model="info.nickName">
				</div>
				<span class="nicknamedes">4-30</span>
				<button @click="register" class="button"></button>
			</div>
		</section>
		<!-- v-forshowlhy -->
		<section id="sex" v-show="sexShow">
			<div class="infoBody">
				<h1></h1>
				<p>hi,{{info.nickName}},</p>
				<h4></h4>
				<button @click="goNext('sexShow','male')" class="button"></button>
				<button @click="goNext('sexShow','female')" class="button"></button>
			</div>
		</section>
		<section id="level" v-show="levelShow">
			<div class="header">
				<span @click="goBack('levelShow')" slot="left">
					<img src="static/img/left.svg" alt="goBack">
				</span>
			</div>
			<div class="infoBody">
				<h2></h2>
				<button  @click="goNext('levelShow','beginner')" class="button">\
					<span class="nicknamedes"></span>
				</button>
				<button  @click="goNext('levelShow','primary')" class="button">\
					<span class="nicknamedes"></span>
				</button>
				<button  @click="goNext('levelShow','medium')" class="button">\
					<span class="nicknamedes"></span>
				</button>
				<button  @click="goNext('levelShow','senior')" class="button">\
					<span class="nicknamedes"></span>
				</button>
			</div>
		</section>
		<section id="BFP" v-show="BFPShow">
			<div class="header">
				<span @click="goBack('BFPShow')" slot="left">
					<img src="static/img/left.svg" alt="goBack">
				</span>
			</div>
			<div class="infoBody">
				<h2></h2>
				<button  @click="goNext('BFPShow','fat')" class="button"></button>
				<button  @click="goNext('BFPShow','thin')" class="button"></button>
				<button  @click="goNext('BFPShow','standar')" class="button"></button>
			</div>
		</section>
		<section id="target" v-show="targetShow">
			<div class="header">
				<span @click="goBack('targetShow')" slot="left">
					<img src="static/img/left.svg" alt="goBack">
				</span>
			</div>
			<div class="infoBody">
				<h2></h2>
				<button  @click="goNext('targetShow','all')" class="button"></button>
				<button  @click="goNext('targetShow','part')" class="button"></button>
			</div>
		</section>
		<section id="limbs" v-show="limbsShow">
			<div class="header">
				<span @click="goBack('limbsShow')" slot="left">
					<img src="static/img/left.svg" alt="goBack">
				</span>
			</div>
			<div class="infoBody">
				<h2></h2>
				<button  @click="goNext('limbsShow','chest')" class="button"></button>
				<button  @click="goNext('limbsShow','backSide')" class="button"></button>
				<button  @click="goNext('limbsShow','abdominal ')" class="button"></button>
				<button  @click="goNext('limbsShow','arm')" class="button"></button>
				<button  @click="goNext('limbsShow','leg')" class="button"></button>
			</div>
		</section>
		<section id="ageWeiHei" v-show="AWHShow">
			<div class="header">
				<span @click="goBack('AWHShow')" slot="left">
					<img src="static/img/left.svg" alt="goBack">
				</span>
			</div>
			<div class="infoBody">
				<h2></h2>
				
				<label>

					<span @click="yearShow = true">{{info.year}}</span>
					<select size="5" v-show="yearShow" v-model="info.year" @change="yearShow = false;" @blur="yearShow = false;">
						<option v-for="(item, index) in yearSlotList" :value="item" :key="index">{{item}}</option>
					</select>
						<span @click="monthShow = true">{{info.month}}</span>
						<select size="4" v-show="monthShow" v-model="info.month" @change="monthShow = false;" @blur="monthShow = false;">
							<option v-for="(item, index) in monthSlotList" :value="item" :key="index">{{item}}</option>
						</select>
						<span @click="insertDay">{{info.day}}</span>
						<select size="4" v-show="dayShow" v-model="info.day" @change="daySlotList = [];dayShow = false;" @blur="dayShow = false;">
							<option v-for="(item, index) in daySlotList" :value="item" :key="index">{{item}}</option>
						</select>					
				</label>
				<label>
					<span @click="heightShow = true;">{{info.height}}cm</span>
					<select size="4" v-show="heightShow" v-model="info.height" @change="heightShow = false;" @blur="heightShow = false;">
						<option v-for="(item, index) in heightSlotList" :value="item" :key="index">{{item}}</option>
					</select>					
				</label>
				<label>
				<span @click="weightShow = true;">{{info.weight}}kg</span>
					<select size="4" v-show="weightShow" v-model="info.weight" @change="weightShow = false;" @blur="weightShow = false;">
						<option v-for="(item,index ) in weightSlotList" :value="item" :key="index">{{item}}</option>
					</select>					
				</label>
				<button @click="goNext('AWHShow')" class="button"></button>
			</div>
		</section>
	</div>
</template>
<script>
import api from '../../fetch/login';
import tips from "../../components/tips.vue";
import axios from "axios";
export default {
	name: 'getUserInfo',
	data() {
		return {
			nickNameShow: true,
			sexShow: false,
			levelShow: false,
			BFPShow: false,
			targetShow: false,
			limbsShow: false,
			AWHShow: false,
			yearShow: false,
			monthShow: false,
			dayShow: false,
			heightShow: false,
			weightShow: false,
			yearSlotList: [],
			monthSlotList: [],
			daySlotList: [],
			weightSlotList: [],
			heightSlotList: [],
			message: "test",
			showTips:false,
			info: {
				header: '../..static/img/logo.svg',
				headerImg: "static/img/headPortrait.svg",
				nickName: '',
				sex: '',
				level: '',
				BFP: '',
				target: '',
				limbs: '',
				year: '---- ',
				month: '-- ',
				day: '-- ',
				height: '--- ',
				weight: '--- ',
			}
		}
	},
	created() {

	},
	mounted() {
		this.insertYear();
		this.insertMonth();
		this.insertWeight();
		this.insertHeight();
		document.getElementsByTagName("select").onblur= ()=>(console.log("152"))
	},
	components: {
		tips
	},
	computed: {
		
		
	},
	methods: {
		goBack(sec) {
			switch (sec) {
				case "AWHShow":
					this.AWHShow = false;
					this.limbsShow = true;
					break;
				case "limbsShow":
					this.limbsShow = false;
					this.targetShow = true;
					break;
				case "targetShow":
					this.targetShow = false;
					this.BFPShow = true;
					break;
				case "BFPShow":
					this.BFPShow = false;
					this.levelShow = true;
					break;
				case "levelShow":
					this.levelShow = false;
					this.sexShow = true;
					break;
				case "sexShow":
					this.sexShow = false;
					this.nickNameShow = true;
					break;
			}
		},
		goNext(sec, val) {
			switch (sec) {
				case "sexShow":
					this.sexShow = false;
					this.levelShow = true;
					this.info.sex = val;
					break;
				case "levelShow":
					this.levelShow = false;
					this.BFPShow = true;
					this.info.level = val;
					break;
				case "BFPShow":
					this.BFPShow = false;
					this.targetShow = true;
					this.info.BFP = val;
					break;
				case "targetShow":
					this.targetShow = false;
					this.limbsShow = true;
					this.info.target = val;
					break;
				case "limbsShow":
					this.limbsShow = false;
					this.AWHShow = true;
					this.info.limbs = val;
					break;
				case "AWHShow":
					localStorage.info = JSON.stringify(this.info);
					this.updataInfo();
					break;
			}
		},
		insertYear() {
			this.yearSlotList = []
			for (let i = 1950; i < new Date().getFullYear(); i++) {
				this.yearSlotList.push(i);
			};	
		},
		insertMonth() {
			this.monthSlotList = []
			for (let i = 1; i < 13; i++) {
				this.monthSlotList.push(i);
			};	
		},
		insertDay() {
			this.daySlotList = []
			this.dayShow = true;
			switch (this.info.month) {
				case 1:
				case 3:
				case 5:
				case 7:
				case 8:
				case 10:
				case 12:
					for (let i = 1; i < 31; i++) {
						this.daySlotList.push(i);
					};
					break;
				case 2:
					for (let i = 1; i < 29; i++) {
						this.daySlotList.push(i);
					};
					if(this.info.year%4 == 0) {
						this.daySlotList.push(29);
					}
					break;
				default:
					for (let i = 1; i < 30; i++) {
						this.daySlotList.push(i);
					};
					break;
			}
		},
		insertWeight(){	
			this.weightSlotList = []
			for (let i = 40; i < 130; i++) {
				this.weightSlotList.push(i);
			};
			
		},
		insertHeight(){	
			this.heightSlotList = []
			for (let i = 120; i < 200; i++) {
				this.heightSlotList.push(i);
			};
			
		},
		register() {
			let id_json = JSON.parse(localStorage.userInfo);
			let data = {
				nickName: this.info.nickName,
				headerImg: this.info.headerImg,
				user_id: id_json.id
			};
			api.register(data)
				.then(res => {
					if(res.message == "success"){
						this.message = "";
						this.showTips = true;
						this.nickNameShow = false;
						this.sexShow = true;
						setTimeout(() => {
							this.showTips = false;
						}, 500)
						
					}
				}, res => {
					this.message = "";
					this.showTips = true;
					setTimeout(() => {
						this.showTips = false;
					}, 500)
				})
		},
		uploadImg(event){
			let data = new FormData();
			data.append("file", event.target.files[0]);
			data.append("type", "header-img");
			api.uploadImg(data)
				.then(res => {
					this.info.headerImg = "http://p24g7dbyx.bkt.clouddn.com/" + res.message;
				})
				.catch(err => {
					throw new Error(err);
				})
		},
		updataInfo(){
			let data = this.info;
			let id_json = JSON.parse(localStorage.userInfo);
			data.user_id = id_json.id;
			api.updataInfo(data)
				.then( res => {
					if(res.message == "success"){
						this.showTips = true;
						this.message = "";
						setTimeout(() => {
							this.showTips = false;
							this.$router.push('/home/date/mall');
						}, 1000);
					}
				})
				.catch(err => {
					this.showTips = true;
					this.message = "";
					setTimeout(() => {
						this.showTips = false;
					}, 1000);
				})
		}

	}
}
</script>
<style  lang="scss" scoped>
.header {
	margin: .1rem 0 .1rem .1rem;			
}
.button {
	width: 80vw;
	border-radius: 50px;
	// height: 40px;
	padding: 0.1rem;
	background-color: white;
	border: 1px solid red;
	color: red;
}

.infoBody {
	display: flex;
	flex-direction: column;
	padding: 0.3rem 0.3rem;
	span {
		display: inline-block;
		text-align: right;
		width: .8rem;
		// height: .5rem;
	}
	.nicknamedes {
		font-size: .1rem;
		color: #ccc;
		text-align: center;
		width: 75vw;
	}
	select {
		position: fixed;
		background-color: rgba(0,0,0,.1);
		bottom: 0;
		right: 0;
		width: 100vw;
		text-align: center;
	}
	p {
		margin: .3rem 0;
	}
	.upload {
		width: 20px;
		height: 20px;
		opacity: 0;
		// position: absolute;

	}
	.avatarGroup {
		display: flex;
		flex-direction: row;
		margin: 0.2rem 0;
		align-items: center;
		img {
			height: 18vh;
			border-radius: 50%;
		}
	}
	button {
		margin-top: 0.6rem;
	}
	.nickname {
		@extend .avatarGroup;
		input {
			border: 0px solid white;
			border-bottom: 1px solid #ccc;
			margin-left: 0.1rem;
			outline: none;
			&:focus {
				border: 1px solid white;
				border-bottom: 1px solid #ccc;
			}
		}
	}
}

</style>


